
.wave--profile-pic[data-v-1b32c318] {
  border-radius: 50%;
  left: 5px;
  position: absolute;
  top: 5px;
  width: 100px;
  z-index: 1;
}
.wave-true[data-v-1b32c318] {
  border: 5px solid #299dcf;
  top: 0;
  left: 0;
  cursor: pointer;
}
.wave-false[data-v-1b32c318] {
  border: 5px solid grey;
  top: 0;
  left: 0;
  pointer-events: none;
}
.pulse[data-v-1b32c318] {
  animation: pulse 2s infinite;
  box-shadow: 0 0 0 rgba(41, 157, 207, 0.4);
}
@keyframes pulse {
0% {
    box-shadow: 0 0 0 0 rgba(41, 157, 207, 0.4);
}
70% {
    box-shadow: 0 0 0 10px rgba(41, 157, 207, 0);
}
100% {
    box-shadow: 0 0 0 0 rgba(41, 157, 207, 0);
}
}
@keyframes spin {
100% {
    transform: rotate(360deg);
}
}
.wave--playing[data-v-1b32c318] {
  border: none;
  top: 5px;
  left: 5px;
  animation: spin 2s linear infinite;
}
.timer[data-v-1b32c318] {
  background-size: 100px 100px;
  background-repeat: no-repeat;
  border-radius: 100%;
  width: 110px;
  height: 110px;
  position: relative;
  z-index: 1;
  margin: 0 5px;
}
.timer--rect[data-v-1b32c318] {
  position: absolute;
  width: 110px;
  height: 110px;
  clip: rect(0px, 110px, 110px, 55px);
  border-radius: 100%;
  background-color: #299dcf;
}
.fill[data-v-1b32c318] {
  background-color: #17385e;
  position: absolute;
  width: 110px;
  height: 110px;
  border-radius: 100%;
  clip: rect(0px, 55px, 110px, 0px);
}
@keyframes left {
0% {
    -webkit-transform: rotate(0deg);
}
100% {
    transform: rotate(180deg);
}
}
.timer--left .fill[data-v-1b32c318] {
  animation: left 15s linear both;
}
.timer--right[data-v-1b32c318] {
  transform: rotate(180deg);
}
@keyframes right {
0% {
    -webkit-transform: rotate(0deg);
}
100% {
    transform: rotate(180deg);
}
}
.timer--right .fill[data-v-1b32c318] {
  animation: right 15s linear both;
  animation-delay: 15s;
}



