
@import url("https://fonts.googleapis.com/css?family=Comfortaa|Raleway");
.margin {
  margin: 10px;
}
.white {
  color: white;
}
.inline {
  display: inline-block;
}
.block {
  display: block;
  margin: 10px auto;
}
.logo-font {
  font-family: 'Comfortaa', cursive;
  font-weight: normal;
}
.btn {
  font-family: 'Raleway', sans-serif;
  background-color: white;
  border: 2px solid black;
  border-radius: 2px;
  cursor: pointer;
  display: inline-block;
  font-size: 18px;
  padding: 6px 10px;
  transition: 0.3s;
}
.btn--main {
  background-color: #299dcf;
  border: 2px solid #006692;
  color: white;
}
.btn--main:hover {
  background-color: #17385e;
}
.btn--secondary {
  background-color: #17385e;
  border: 2px solid #299dcf;
  color: white;
}
.btn--secondary span, .btn--secondary a {
  color: white;
  text-decoration: none;
}
.btn--secondary:hover {
  background-color: #299dcf;
}
.btn--disabled {
  background-color: white;
  border: 2px solid #94c5da;
  color: #94c5da;
}
.btn--large {
  font-size: 18px;
  padding: 8px 10px;
}
.tag {
  font-family: 'Raleway', sans-serif;
  color: #299dcf;
  cursor: pointer;
  display: inline-block;
  letter-spacing: 1px;
  font-size: 13px;
  font-weight: bold;
  margin-right: 10px;
  transition: 0.3s;
}
.tag:hover {
  color: #17385e;
}
.search-box {
  border: 0;
  border: 1px solid #299dcf;
  border-radius: 2px;
  padding: 5px;
  width: 265px;
  font-size: 14px;
}
.search-box::-webkit-input-placeholder {
  color: #94c5da;
}
.search-box::-moz-placeholder {
  color: #94c5da;
}
.help.is-danger {
  color: red;
  display: block;
  font-size: 12px;
  margin-bottom: 12px;
}
.button {
  background-color: #299dcf;
  border: 2px solid #17385e;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  display: inline-block;
  font-size: 18px;
  padding: 2px 10px;
  margin-right: 10px;
  text-decoration: none;
}
.playlist--all-containers {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.playlist--container {
  background-color: #e8e8e8;
  border-radius: 5px;
  display: inline-block;
  float: left;
  height: auto;
  margin: 5px;
  min-height: 140px;
  padding: 5px 5px;
  padding-top: 0;
  max-width: 200px;
  vertical-align: top;
}
.playlist--title {
  display: block;
  color: #17385e;
  max-width: 200px;
}
.playlist--art {
  display: inline-block;
  height: 200px;
  float: left;
  max-width: 200px;
  padding: 5px 0;
}
.playlist--spotify {
  display: block;
  float: left;
  margin: auto;
  width: 100px;
}
.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.25s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
  transition: opacity 0.25s;
}
.block {
  display: block;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
.mini-toastr {
  bottom: 12px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  top: auto !important;
}
.mini-toastr__notification {
  animation: fadein 0.5s;
  opacity: 1 !important;
}
@keyframes fadein {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
body {
  margin: 0;
}
h1, h2, h3, h4 {
  font-family: 'Raleway', sans-serif;
  margin: 5px 0;
}

