
.nav-bar--link {
  border-bottom: 2px solid white;
  float: left;
  margin-left: 10px;
  color: #fff;
  font-size: 20px;
  text-decoration: none;
  transition: 0.2s;
}
.nav-bar--link__logout {
  float: right;
}
.nav-bar--link:hover {
  border-bottom: 2px solid #17375c;
  transition: 0.2s;
}
.nav-bar--login {
  float: right;
}
.active {
  border-bottom: 2px solid #17385e;
}
.nav-bar {
  background-color: #299dcf;
  margin: 0;
  padding: 5px 1%;
  width: 98%;
}
.nav-bar:after {
  content: "";
  display: block;
  clear: both;
}
.nav-links-ul {
  display: inline-block;
  float: left;
  list-style-type: none;
  margin-bottom: 0;
  padding-left: 14px;
}
@media screen and (max-width: 768px) {
.nav-links-ul {
      float: none;
      margin-top: 8px;
      padding-left: 0;
}
}
.nav-links-ul li {
  display: inline-block;
}
.login:hover {
  color: #17375c;
  cursor: pointer;
}
.nav-bar--heading {
  color: #fff;
  display: inline-block;
  font-size: 49px;
  float: left;
  text-decoration: none;
  margin: 0;
  padding-left: 10px;
}
@media screen and (max-width: 768px) {
.nav-bar--heading {
      display: none;
}
}
.nav-bar--logo {
  float: left;
  width: 42px;
}



